import React from 'react';
import { courseData } from '../data/data';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});


export default function BasicTable() {
    const classes = useStyles();

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Subject Code</TableCell>
                        <TableCell align="right">Course Number</TableCell>
                        <TableCell align="right">CRN</TableCell>
                        <TableCell align="right">Course Title</TableCell>
                        <TableCell align="right">Credits</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {courseData.map((data, key) => (
                        <TableRow key={key}>
                            <TableCell component="th" scope="row">
                                {data.subject_code}
                            </TableCell>
                            <TableCell align="right">{data.course_number}</TableCell>
                            <TableCell align="right">{data.crn}</TableCell>
                            <TableCell align="right">{data.course_title}</TableCell>
                            <TableCell align="right">{data.credits}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}